import React from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../../layout/MiniBox';
import Status from '../../../../components/Status';
import style from './CardCreated.module.css';
import { useAppSelector } from '../../../../hooks/store';
import { camel2title } from '../../../../utils/general';
import { useDrawer } from '../../../../hooks/drawer';
import { eventEmitter, Events } from '../../../../utils/events';

function CardCreated() {
  const { t } = useTranslation();
  const { close } = useDrawer();

  const cardPayloadData = useAppSelector((store) => store.card.selectedCard, shallowEqual);
  const replaceCardId = useAppSelector((store) => store.card.replaceCardId);

  return (
    <div className="page">
      <MiniBox>
        <Status
          status="success"
          title={t(!replaceCardId ? (cardPayloadData?.cardType === 'physical' ? 'create_card_success.title_physical' : 'create_card_success.title_virtual') : 'create_card_success.replace_title')}
          ctaText={t('create_card_success.ctaText')}
          onNext={() => {
            close();

            eventEmitter.dispatch(Events.CARD_REFRESH, {});
          }}
        >
          <Box className={style.card_info}>
            <HStack className={style.card_info_item}>
              <Text w="50%" variant="info">{t('create_card_success.cardLabel')}</Text>
              <Text w="50%" variant="regular">{cardPayloadData?.label}</Text>
            </HStack>
            <HStack className={style.card_info_item}>
              <Text w="50%" variant="info">{t('create_card_success.spendingLimit')}</Text>
              <Text w="50%" variant="regular">
                $
                {cardPayloadData?.limitAmount}
                {' '}
                {cardPayloadData?.limitInterval}
              </Text>
            </HStack>
            <HStack className={style.card_info_item}>
              <Text w="50%" variant="info">{t('create_card_success.status')}</Text>
              <Text w="50%" variant="regular">{camel2title(cardPayloadData?.cardStatus || '')}</Text>
            </HStack>
          </Box>
        </Status>
      </MiniBox>
    </div>
  );
}

export default CardCreated;
